<template>
<section class="banner">
  <div class="banner-top">
    <div class="banner-top-moon"></div>
      <div class="banner-top-light"></div>
    <div class="banner-top-main"></div>
  </div>
  <Navbar :items="items" :newName="newName"></Navbar>
</section>
</template>
<script>
import Navbar from "../core/Navbar"
export default {
  name:"Banner",
  components:{
    Navbar
  },
  data(){
    return{
      newName:"banner-banner",
      items:[
        {
          src:"./static/banner-img1.png",
          link:"/"
        },
        {
          src:"./static/banner-img2.png",
          link:"/"
        },
        {
          src:"./static/banner-img3.png",
          link:"/"
        },
        {
          src:"./static/banner-img4.png",
          link:"/"
        }
      ]
    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
@keyframes swing{
  form,to{
    transform: rotateZ(0);
  }
  25%{
    transform: rotateZ(10deg);
  }
  75%{
    transform: rotateZ(-10deg);
  }
}
.bg(){
  background-position: top center;
  background-repeat: no-repeat;
  background-size: cover;
}
.banner{
  .banner-top{position: relative;
    height: 740px;
    .bg;
    background-image: url("./static/banner-bg1.png");
    .banner-top-moon{
      position: absolute;
      width: 600px;
      top:40px;
      left: 50%;
      height: 300px;
      margin-left: -300px;
      .bg;
      background-image: url("./static/banner-moon.png");
    }
    .banner-top-light{
      background-image: url("./static/banner-light.png");
      position: absolute;
      height: 540px;
      width: 540px;
      left: 50%;
      margin-left: -270px;
      top: -10px;
      .bg;
      transform-origin: top center;
      animation: swing 4s linear infinite;
    }
    .banner-top-main{
      .bg;
      position: absolute;
      background-image: url("./static/banner-bg2.png");
      width: 500px;
      left: 50%;
      margin-left: -250px;
      top: 88px;
      height: 660px;
    }

  }


}
</style>



