<template>
  <Panel :title="title">
    <router-link slot="title-right" class="look-more" tag="a" :to="{name:'Home'}">更多尖货 </router-link>
    <Swiper :items="items" :options="option" class="product-list"></Swiper>
  </Panel>
</template>
<script>
  import Panel from "../core/Panel"
  import Swiper from "../core/Swiper"

export default {
  name:"Crowdfunding",
  components:{
    Panel,
    Swiper
  },
  data(){
    return{
      title:"众筹新品",
      option:{
        slidesOffsetBefore:32,
        slidesOffsetAfter:32,
        pagination:'',
        spaceBetween:24,
        autoplay:false,
        slidesPerView:2.5,
        loop:false
      },
      items:[
        {
          src:"./static/newproduct1.jpg",
          alt:"新品",
          link:"/banner"
        },
        {
          src:"./static/newproduct2.jpg",
          alt:"新品",
          link:"/banner"
        },
        {
          src:"./static/newproduct3.jpg",
          alt:"新品",
          link:"/banner"
        },
        {
          src:"./static/newproduct4.jpg",
          alt:"新品",
          link:"/banner"
        }
      ]
    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
.look-more{
position: absolute;
color: #999;
right:32px;
line-height: 112px;
display: inline-block;
padding:0 32px;
font-size: 28px;
background: url("~@/assets/icon_arrow1.png") center right no-repeat;
background-size: 18px 28px;
}
.product-list{
  width: 100%;
}
</style>



