<template>
  <swiper class="swiper" :options="options" :not-next-tick="options.notNextTick">
      <swiper-slide v-for="item in items" :key="item.src">
        <router-link class="swiper-links"  :to="item.link">
        <img :src="item.src" :alt="item.alt" />
        </router-link>
      </swiper-slide>
<div class="swiper-pagination" v-if="options.pagination"  slot="pagination"></div>
  </swiper>


</template>
<script>
import { swiper, swiperSlide } from '../../../node_modules/vue-awesome-swiper/dist/vue-awesome-swiper'
import '../../../node_modules/swiper/dist/css/swiper.css'
export default {
  name:"Swiper",
  components:{
  swiper,swiperSlide
  },
  props:{
    options:{
      type:Object,
      default(){
        return{
          pagination:{el:".swiper-pagination"},
          autoplay:true,
          speed:300,
          loop:true,
          notNextTick:true,
          direction:"horizontal",
          autoHeight:true,
          slidesOffsetBefore:0,
          slidesOffsetAfter:0,
          slidesPerView:1,
          spaceBetween:0
        }
      }
    },
    items:{
      type:Array,
      default(){
        return[{src:"",alt:"",link:""}]
      }
    }
  },
  data(){
    return{
    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
.swiper{
width: 100%;
.swiper-links{
  display: block;
  width: 100%;
  img{
display: block;
width: 100%;
  }
}
}


</style>



