<template>
<Panel :title="title" :newName="newName">
  <Swiper :options="options" :items="items"></Swiper>
<img class="banner" src="../../../static/insure-banner.jpg"/>
</Panel>
</template>
<script>
import Panel from "../core/Panel"
import Swiper from "../core/Swiper"
export default {
  name:"Insurance",
  components:{
    Panel,
    Swiper
  },
  data(){
    return{
      newName:"panel-padding",
      title:"保险保障",
      options:{
        slidesOffsetBefore:32,
        slidesOffsetAfter:32,
        pagination:'',
        spaceBetween:24,
        autoplay:false,
        slidesPerView:2.2,
        loop:false
      },
      items:[
        {
          src:"./static/insurance1.jpg",
          alt:"保险保障",
          link:"/banner"
        },
        {
          src:"./static/insurance2.jpg",
          alt:"保险保障",
          link:"/banner"
        },
        {
          src:"./static/insurance3.jpg",
          alt:"保险保障",
          link:"/banner"
        },
        {
          src:"./static/insurance4.png",
          alt:"保险保障",
          link:"/"
        },
        {
          src:"./static/insurance5.jpg",
          alt:"保险保障",
          link:"/"
        }
      ]

    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
.banner{
display: block;
width: 100%;
margin-top:40px;
 }

</style>



