<template>
  <div class="money-header" >
    <Swiper :items="items"></Swiper >
    <img class="money-header-banner" src="../../../static/money-banner.png"/>

  </div>

</template>
<script>
import Swiper from "../core/Swiper"

export default {
  name:"MoneySlide",
  components:{
    Swiper
  },
  data(){
    return{
      options:{
        speed:300
      },
      items:[
        {
          src:"./static/money-banner1.jpg",
          alt:"第一张图片",
          link:"/banner"
        },
        {
          src:"./static/money-banner2.jpg",
          alt:"第二张图片",
          link:"/banner"
        },
        {
          src:"./static/money-banner3.jpg",
          alt:"第三张图片",
          link:"/banner"
        }
      ]

    }
  }
}
</script>
<style lang="less" type="text/less" scoped>
.money-header{
  .money-header-banner{
    display: block;
    width: 100%;
    background-color: #fff;
  }
  }
</style>



